    var nextYear = nextMonthObj.getFullYear();
    var nextMonth = nextMonthObj.getMonth() + 1;

    var startOfDay = targetDate.startOfDay();

    var currentMonth = options.month;
    var endOfMonthDate = targetDate.getLastDate();

    var date = 1;
