        var endMonth = _endDate.getMonth() + 1;
        var startDate = _startDate.getDate();
        var endDate = _endDate.getDate();
        var startNum = 1;
        var endNum = endDate;

        var firstTileValue = firstTileYear * 10000 + firstTileMonth * 100 + firstTileDate;
        var startDateValue = startYear * 10000 + startMonth * 100 + startDate;
        var endDateValue = endYear * 10000 + endMonth * 100 + endDate;
        var endTileValue = currentYear * 10000 + (indexOfCurrentMonth + 1) * 100 + eNum;

        if (startDateValue > endTileValue) {
            return;
        }
        if (endDateValue < firstTileValue) {
            return;
        }
        if (endDateValue > endTileValue) {
            endNum = eNum;
        }
