        var monthEndDate = new Date(currentYear + '-' + (indexOfCurrentMonth + 1).pad(2) + '-' + endOfMonthDate);
        var endOfMonthDay = monthEndDate.getDay();

        var startYear = _startDate.getFullYear();
        var endYear = _endDate.getFullYear();
        var startMonth = _startDate.getMonth() + 1;
        var endMonth = _endDate.getMonth() + 1;
        var startDate = _startDate.getDate();
        var endDate = _endDate.getDate();

        var lastTileYear = currentYear;
        var lastTileMonth = indexOfCurrentMonth + 2;
        var lastTileDate = 6 - endOfMonthDay;
        if (lastTileMonth > 12) {
            lastTileYear += 1;
            lastTileMonth -= 12;
        }

        var firstTileValue = firstTileYear * 10000 + firstTileMonth * 100 + firstTileDate;
        var lastTileValue = lastTileYear * 10000 + lastTileMonth * 100 + lastTileDate;
